import request from '@/static/request.js'
export default {
  data() {
    const currentDate = this.getDate({
      format: true
    })
    return {
      id: 0,
      userId: 0,
			campClasses: [],
      post: '',
      way: '',
      name: '',
      wechat: '',
      endDate: currentDate,
      birthday: '年-月-日',
      gender: '',
      education: '',
      classes: '',
      educationTime: '',
      experience: '',
      email: '',
      telephone: '',
      city: '',
      brand: '',
      website: '',
      application: '',
      date: '年-月-日',
      companyCity: '',
      membership: '',
      abstract: '',
      output: '',
      income: '',
      finance: '',
      price: '',
      addition: '',
      referee1: '',
      referee2: '',
      referee3: '',
      ifagree: '',
      reason: '',
      contribution: '',
      showAlert: false,
    }
  },
  methods: {
    genderChange(e) {
      const vm = this
      vm.gender = e.currentTarget.dataset.gender
    },
    educationChange(e) {
      const vm = this
      vm.education = e.currentTarget.dataset.education
    },
    classesChange(e) {
      const vm = this
      vm.classes = e.currentTarget.dataset.classes
    },
    financeChange(e) {
      const vm = this
      vm.finance = e.currentTarget.dataset.finance
    },
    bindDateChange: function(e) {
      this.date = e.target.value
    },
    birthdayChange: function(e) {
      this.birthday = e.target.value
    },
    postChange(e) {
      const vm = this
      vm.post = e.currentTarget.dataset.post
    },
    wayChange(e) {
      const vm = this
      vm.way = e.currentTarget.dataset.way
    },
    checkEmail() {
      const mail = this.email
      const reg = /^[A-Za-z0-9\u4e00-\u9fa5]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/
      if(reg.test(mail) === true) {
        this.showAlert = false
      }
      else this.showAlert = true
    },
    getDate(type) {
      const date = new Date();
      let year = date.getFullYear();
      let month = date.getMonth() + 1;
      let day = date.getDate();
      if (type === 'start') {
        year = year - 60;
      } else if (type === 'end') {
        year = year + 2;
      }
      month = month > 9 ? month : '0' + month;
      day = day > 9 ? day : '0' + day;
      return `${year}-${month}-${day}`;
    },
    
    saveMessage() {
      const vm = this
      request
        .get('alterApplyData/', {
          user_id: vm.userId,
          user_birthday: vm.birthday,
          user_post: vm.post,
          user_referee1_message: vm.referee1,
          user_referee2_message: vm.referee2,
          user_referee3_message: vm.referee3,
          user_other_tips_message_question_for_absenceitem: vm.ifagree,
          reasons_for_signin: vm.reason,
          contribution_to_others: vm.contribution,
          messageway_to_camp: vm.way,
          educated_condition: vm.education,
          user_educondition_time_detials: vm.educationTime,
          user_beforestartup_company_condition: vm.experience,
          user_gender:vm.gender,
          user_weixinnumber: vm.wechat,
          user_telephone: vm.telephone,
          user_email: vm.email,
          user_name: vm.name,
          user_company: vm.brand,
          user_city: vm.city,
          website: vm.website,
          app_or_publicAccounts: vm.application,
          startup_time: vm.date,
          center_city: vm.companyCity,
          membership: vm.membership,
          abstract: vm.abstract,
          main_operational_data: vm.output,
          income_scale: vm.income,
          financing_situation: vm.finance,
          value_of_assessment: vm.price,
          additions: vm.addition,
        })
        .then(res => {
          if(res.data.statuscode === '400') {
            uni.showToast({
              title: '报名失败',
              duration: 1000,
            })
          }
          else if(res.data.statuscode === '200') {
            uni.showToast({
              title: '报名成功！',
              duration: 1000,
            })
            uni.redirectTo({
              url: '../apply/campDetail'
            })
          }
					else {
						uni.showToast({
							title: '请检查信息是否填写完整！',
							duration: 1000,
						})
					}
        })
      request
        .get('signupCamp',{
			    user_id: vm.userId,
			    camp_id: vm.id,
          class_id: vm.classes,
        })
        .then(res => {
          if(res.data.statuscode === '200') {  
            uni.showToast({
              title: '报名成功',
              duration: 1000
            })
          }
        })
    }
  },
  onLoad(options) {
    uni.getStorage({
      key: 'GetId',
      success: res => {
        this.userId = res.data
      }
    }),
    uni.getStorage({
      key: 'CampId',
      success: res => {
        this.id = res.data
				request
					.post('queryCampClass/', {
						camp_id: this.id,
					})
					.then(res => {
						this.campClasses = res.data
					})
      }
    })
  },
}
